.header {
  position: relative;
    width: 100vw;
    height: 33vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(/src/assets/images/image10.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: brightness(60%);

}
  .headerTitles {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 100%;
    align-items: center;
    font-family: "Roboto", sans-serif;
    color: black;
  }
  
  .headerTitleSm {
    font-family: "Princess Sofia";
    font-size: 32px;
    font-weight: 600;
  }
  .headerTitleLg {
    font-size: 48px;
    font-weight: 400;
  }
  
  .headerImage {
    width: 100%;
    height: 350px;
    margin-top: 1px;
    object-fit: cover;
  }
  